import React from 'react'
// "homepage": "https://abrahamJiutasa.github.io/production",
import {
  BlogContainer,
  BlogWrapper,
  BlogHeading,
  Topline,
  Header,
  Col,
  Date,
  Subtext,
  Button,
  BlogButton,
  BlogText,
  Img,
} from './BlogsElements'

const Blogs = (dataBlog) => {
  console.log(dataBlog)
  let column = dataBlog.dataBlog.map((i) => (
    <Col key={i.key}>
      <Img src={i.img} alt={i.alt} />
      <Date theme={i.theme}>{i.date}</Date>
      <Header>{i.header}</Header>
      <Subtext>{i.subText}</Subtext>
      <Button theme={i.theme}>Read more...</Button>
    </Col>
  ))
  return (
    <React.Fragment>
      <BlogContainer>
        <Topline> Culture</Topline>
        <BlogHeading>Our latest blogs</BlogHeading>
        <BlogText>
          {' '}
          Get an inside look at the people, places and ideas that move us
        </BlogText>
        <BlogButton>View</BlogButton>
        <BlogWrapper>{column}</BlogWrapper>
      </BlogContainer>
    </React.Fragment>
  )
}

export default Blogs
