import React from 'react'
import {
  BlogContainer,
  BlogWrapper1,
  Col1,
  Img,
  Col2,
  Topline,
  Header1,
  Subtext,
  AuthorContainer,
  AuthorImg,
  Author,
  Designation,
  Grid1,
  Col,
  ColImg,
  ColWrapper,
  WrapTopline,
  WrapHeader,
  WrapSubText,
} from './BLogElement'

import dataBlog from './data'

function Blog() {
  console.log(dataBlog)
  let column = dataBlog.map((i) => (
    <Col
      rowStart={i.rowStart}
      minRowStart={i.minRowStart}
      rowEnd={i.rowEnd}
      colStart={i.colStart}
      colEnd={i.colEnd}
      gridTemp={i.gridTemp}
      key={i.key}
    >
      <ColImg
        src={i.img}
        Alt={i.alt}
        minRowStart={i.minRowStart}
        minRowEnd={i.minRowEnd}
        imgRowEnd={i.imgRowEnd}
        imgColStart={i.imgColStart}
        imgColEnd={i.imgColEnd}
        borderRadius={i.borderRadius}
      />
      <ColWrapper
        wrapRowStart={i.wrapRowStart}
        minRowStart={i.minRowStart}
        wrapRowEnd={i.wrapRowEnd}
        wrapColStart={i.wrapColStart}
        wrapColEnd={i.wrapColEnd}
        borderRadius={i.borderRadius}
      >
        <WrapTopline theme={i.theme}>{i.date}</WrapTopline>
        <WrapHeader theme={i.theme}>{i.header}</WrapHeader>
        <WrapSubText>{i.subText}</WrapSubText>
      </ColWrapper>
    </Col>
  ))

  return (
    <React.Fragment>
      <BlogContainer>
        <BlogWrapper1>
          <Grid1>
            <Col1>
              <Img src='logo.png' />
            </Col1>
            <Col2>
              <Topline>26th Jan 2021</Topline>
              <Header1>
                Announcing the Winners of the Urban Pathways Logo Competition
              </Header1>
              <Subtext>
                We were beyond excited, at the end of last week, to finally
                announce the winners of our logo competition. Created to
                inspire, champion and empower youth...
              </Subtext>
              <AuthorContainer>
                <AuthorImg src='logo.png' />
                <Author>fijiyouthculture</Author>
                <Designation>admin</Designation>
              </AuthorContainer>
            </Col2>
          </Grid1>
          {column}
        </BlogWrapper1>
      </BlogContainer>
    </React.Fragment>
  )
}

export default Blog
