import React from 'react'
import {
  TeamContainer,
  TeamWrapper,
  Leader,
  MemberContainer,
  Member,
  MemberImage,
  MemberText,
  Designation,
  LeaderImage,
  LeaderText,
  LeaderDesignation,
  HeadWrapper,
  Header,
  Logo,
  BodyWrapper,
} from './TeamElements'

import data from './data'
function Team() {
  let team = data.map((i) => {
    return (
      <TeamWrapper key={i.headKey}>
        <HeadWrapper>
          <Header>{i.header}</Header>
          <Logo src={i.logo} alt={i.logoAlt} />
        </HeadWrapper>
        <BodyWrapper>
          <Leader>
            <LeaderImage src={i.leaderImg} alt={i.leaderAlt} />
            <LeaderText>{i.leaderText}</LeaderText>
            <LeaderDesignation>{i.leaderDesignation}</LeaderDesignation>
          </Leader>

          <MemberContainer>
            {i.member.map((i) => {
              return (
                <React.Fragment>
                  <Member key={i.memberKey}>
                    <MemberImage src={i.memberImg} alt={i.memberAlt} />
                    <MemberText>{i.memberText}</MemberText>
                    <Designation>{i.memberDesignation}</Designation>
                  </Member>
                </React.Fragment>
              )
            })}
          </MemberContainer>
        </BodyWrapper>
      </TeamWrapper>
    )
  })
  return (
    <React.Fragment>
      <TeamContainer>{team}</TeamContainer>
    </React.Fragment>
  )
}

export default Team
