import styled from 'styled-components'

export const PartnerContainer = styled.div`
  width: 90vw;
  display: block;
  margin-left: auto;
  margin-right: auto;
`

export const Header = styled.h1`
  text-align: center;
  font-size: 35px;
  line-height: 1.1;
  font-weight: 600;
  color: black;

  @media screen and (max-width: 765px) {
    font-size: 20px;
  }
`
export const Subtext = styled.h2`
  text-align: center;
  font-size: 14px;
  line-height: 1.1;
  font-weight: 600;
  margin-bottom: 40px;
  letter-spacing: 1px;
  @media screen and (max-width: 765px) {
    font-size: 18px;
  }
`

export const ImageContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: fit-content;
  margin-bottom: 80px;
  /* background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.6) 0%,
      rgba(0, 0, 0, 0.6) 100%
    ),
    url('uaeLogo.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; */

  @media screen and (max-width: 768px) {
  }
`

export const PartnerName = styled.a`
  text-decoration: none;
  text-align: center;
  font-size: 25px;
  margin-bottom: 40px;
  color: black;

  &:hover {
    color: ${({ theme }) =>
      theme === 'yellow'
        ? '#F3E603'
        : theme === 'green'
        ? '#01BF71'
        : theme === 'brown'
        ? '#704A3E'
        : '#078DE7'};
  }
`

export const Img = styled.img`
  width: 400px;
  height: 400px;
  object-fit: fill;
  border: black 2px solid;
  padding: 20px;
  align-self: center;
  background: #ffff;
`
