import styled from 'styled-components'
import { Link as LinkRouter } from 'react-router-dom'

export const InfoContainer = styled.div`
  color: #fff;
  background: ${({ lightBg }) => (lightBg ? '#f9f9f9' : '#010606')};
  overflow-x: hidden;

  @media screen and (max-width: 768px) {
    padding: 100px 0;
  }
`

export const InfoWrapper = styled.div`
  display: grid;
  z-index: 1;
  height: 860px;
  width: 100%;
  max-width: 1100px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 24px;
  justify-content: center;
`

export const InfoRow = styled.div`
  display: grid;
  grid-auto-columns: minmax(auto, 1fr);
  align-items: center;
  grid-template-areas: ${({ imgStart }) =>
    imgStart ? `"col2 col1"` : `"col1 col2"`};
  @media screen and (max-width: 768px) {
    grid-template-areas: 'col1' 'col2';
  }
`

export const Column1 = styled.div`
  margin-bottom: 15px;
  padding: 0 15px;
  grid-area: col1;
`
export const Column2 = styled.div`
  margin-bottom: 15px;
  padding: 0 15px;
  grid-area: col2;
`

export const TextWrapper = styled.div`
  max-width: 540px;
  padding-top: 0;
  padding-bottom: 60px;
`

export const Topline = styled.p`
  color: ${({ theme }) =>
    theme === 'yellow'
      ? '#F3E603'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  margin-bottom: 16px;
`

export const Heading = styled.h1`
  margin-bottom: 24px;
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  color: ${({ lightText }) => (lightText ? '#f7f8fa' : '#010606')};
  font-family: 'Archivo Black', sans-serif;
  @media screen and (max-width: 480px) {
    font-size: 32px;
  }
`

export const Subtext = styled.p`
  max-width: 440px;
  margin-bottom: 35px;
  font-size: 18px;
  line-height: 24px;
  color: ${({ darktext }) => (darktext ? '#010606' : '#fff')};
`

export const BtnWrapper = styled.div`
  display: flex;
  justify-content: flex-start;
`

export const Button = styled(LinkRouter)`
  width: 100px;
  border-radius: 50px;
  background: ${({ theme }) =>
    theme === 'yellow'
      ? '#F3E603'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
  white-space: nowrap;
  padding: 16px 64px;
  color: #010606;
  font-size: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  text-align: center;
  &:hover {
    transition: all 0.2s ease-in-out;
    background: #f5f5f7;
    color: #010606;
    border: 1px solid;
    border-color: ${({ theme }) =>
      theme === 'yellow'
        ? '#F3E603'
        : theme === 'green'
        ? '#01BF71'
        : theme === 'brown'
        ? '#704A3E'
        : '#078DE7'};
  }
`

export const ImgWrap = styled.div`
  max-width: 555px;
  height: 100%;
`

export const Img = styled.img`
  max-width: 600px;
  height: 700px;
  margin: 0 0 10px 0;
  padding-right: 0;

  @media screen and (max-width: 768px) {
    margin-top: 40px;
    max-width: 500px;
    height: 400px;
  }
`
