import styled from 'styled-components'
// import { Link as LinkRouter } from 'react-router-dom'

export const BlogContainer = styled.div`
  width: 90vw;
  display: block;
  margin-left: auto;
  margin-right: auto;
`

export const BlogWrapper1 = styled.div`
  gap: 20px;
  margin-top: 0px;
  width: 95%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 378px 200px 200px 200px 200px;
  /* border: #5b6066 2px solid; */
  background: #ffff;
  overflow-x: hidden;
  padding: 20px 30px;
  @media screen and (max-width: 790px) {
    grid-template-columns: 1fr;
    grid-template-rows: 200vw 140vw 140vw 140vw 140vw 140vw 140vw 140vw 140vw;
    gap: 30px;
    margin-left: -20px;
    height: 3400px;
  }
`
export const Grid1 = styled.div`
  border-radius: 20px;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  @media screen and (max-width: 750px) {
    grid-template-columns: 1fr;
    grid-template-rows: 80vw 80vw;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    border: 2px solid black;
    /* margin-bottom: 300px; */
  }
`
export const Col1 = styled.div`
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  @media screen and (max-width: 790px) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
  }
`
export const Img = styled.img`
  width: 100%;
  height: 378px;
  object-fit: fill;
  border-radius: 20px 0px 0px 20px;
  @media screen and (max-width: 790px) {
    width: 80vw;
    height: 80vw;
  }
`
export const Col2 = styled.div`
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  display: flex;
  flex-direction: column;
  padding: 20px 20px;
  @media screen and (max-width: 768px) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
  }
`

export const Header1 = styled.h1`
  font-size: 20px;
  color: black;
  text-align: center;
  align-self: center;
`

export const Subtext = styled.h2`
  font-size: 15px;
  color: #5b6066;
  text-align: center;
  align-self: center;
  margin-bottom: 40px;
`

export const AuthorContainer = styled.div`
  text-align: center;
  align-self: center;
  width: 100%;
  display: flex;
  flex-direction: column;
`

export const AuthorImg = styled.img`
  width: 40px;
  height: 40px;
  border-radius: 50% 50%;
  align-self: center;
  border: 1px solid black;
  padding: 2px;
`

export const Author = styled.h2`
  font-size: 10px;
  text-align: center;
`

export const Designation = styled.p`
  font-size: 10px;
  color: gray;
`

export const Topline = styled.p`
  color: ${({ theme }) =>
    theme === 'yellow'
      ? '#F3E603'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1.4px;
  text-align: center;
  @media screen and (max-width: 768px) {
    margin-left: 30px;
  }
`
////SHORT HORIZONTAL BARS/////
export const Col = styled.div`
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  grid-column-start: ${({ colStart }) => (colStart > 0 ? colStart : 0)};
  grid-column-end: ${({ colEnd }) => (colEnd > 0 ? colEnd : 0)};
  grid-row-start: ${({ rowStart }) => (rowStart > 0 ? rowStart : 0)};
  grid-row-end: ${({ rowEnd }) => (rowEnd > 0 ? rowEnd : 0)};
  display: grid;
  grid-template-columns: ${({ gridTemp }) =>
    gridTemp === 'horizontal' ? '1fr 1fr 1fr' : '1fr'};
  grid-template-rows: ${({ gridTemp }) =>
    gridTemp === 'horizontal' ? '1fr' : '200px 1fr 1fr'};
  border-radius: 20px;
  @media screen and (max-width: 768px) {
    grid-template-columns: 1fr;
    grid-template-rows: 80vw 80vw;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: ${({ minRowStart }) =>
      minRowStart > 0 ? minRowStart : minRowStart};
    grid-row-end: ${({ minRowEnd }) => (minRowEnd > 0 ? minRowEnd : minRowEnd)};
  }
`
export const ColImg = styled.img`
  grid-row-start: ${({ imgRowStart }) => (imgRowStart > 0 ? imgRowStart : 0)};
  grid-row-end: ${({ imgRowEnd }) => (imgRowEnd > 0 ? imgRowEnd : 0)};
  grid-column-start: ${({ imgColStart }) =>
    imgColStart > 0 ? imgColStart : 0};
  grid-column-end: ${({ imgColEnd }) => (imgColEnd > 0 ? imgColEnd : 0)};
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: ${({ borderRadius }) =>
    borderRadius === 'horizontal'
      ? '20px 0px 0px 20px'
      : borderRadius === 'vertical'
      ? '20px 20px 0px 0px'
      : '0px 20px 20px 0px'};

  @media screen and (max-width: 768px) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    border-radius: 20px 20px 0px 0px;
    height: 80vw;
    width: 100%;
  }
`
export const ColWrapper = styled.div`
  grid-row-start: ${({ wrapRowStart }) =>
    wrapRowStart > 0 ? wrapRowStart : wrapRowStart};
  grid-row-end: ${({ wrapRowEnd }) =>
    wrapRowEnd > 0 ? wrapRowEnd : wrapRowEnd};
  grid-column-start: ${({ wrapColStart }) =>
    wrapColStart > 0 ? wrapColStart : wrapColStart};
  grid-column-end: ${({ wrapColEnd }) =>
    wrapColEnd > 0 ? wrapColEnd : wrapColEnd};
  border-radius: ${({ borderRadius }) =>
    borderRadius === 'horizontal'
      ? '0px 20px 20px 0px'
      : borderRadius === 'vertical'
      ? '0px 0px 20px 20px'
      : '20px 0px 0px 20px'};
  padding: 5px;
  text-align: center;

  @media screen and (max-width: 768px) {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
    height: 80vw;
  }
`
export const WrapTopline = styled.p`
  font-size: 10px;
  color: ${({ theme }) =>
    theme === 'yellow'
      ? 'coral'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
`
export const WrapHeader = styled.h1`
  font-size: 20px;
  &:hover {
    color: ${({ theme }) =>
      theme === 'yellow'
        ? 'coral'
        : theme === 'green'
        ? '#01BF71'
        : theme === 'brown'
        ? '#704A3E'
        : '#078DE7'};
  }
  @media screen and (max-width: 768px) {
    width: 100%;
  }
`

export const WrapSubText = styled.p`
  font-size: 14px;
  color: #5b6066;
`
