import styled from 'styled-components'

export const HeroContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  z-index: 1;
  position: relative;
  padding: 0 30px;

  /* add :before styles */
`
export const HeroBg = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
`

export const VideoBg = styled.video`
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  background: #232a34;
  filter: brightness(60%);
`
export const HeroContent = styled.div`
  margin-top: -100px;
  z-index: 3;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  overflow: hidden;
`

export const HeroH1 = styled.h1`
  margin-left: 20px;
  font-family: 'Rochester', cursive;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
  font-size: 12vw;
  text-align: center;
  color: #f1f1f1;
  background: transparent;
  z-index: 4;
  margin-top: 0px;
  /* -webkit-text-stroke-width: 0.1px;
  -webkit-text-stroke-color: black; */
`

export const HeroP = styled.p`
  font-size: 4vw;
  text-align: center;
  color: #f1f1f1;
  z-index: 4;
`
