import React from 'react'
import HeroSection from '../components/HeroSection'
import InfoSection from '../components/InfoSection'
import Blogs from '../components/Blogs'

import { dataBlog } from '../components/Blogs/data'
import {
  dataAbout,
  dataJoin,
  dataPartners,
  dataTeam,
} from '../components/InfoSection/data'

const Home = () => {
  return (
    <div>
      <HeroSection />
      <InfoSection {...dataAbout} />
      <InfoSection {...dataTeam} />
      <InfoSection {...dataPartners} />
      <InfoSection {...dataJoin} />
      <Blogs dataBlog={dataBlog} />
    </div>
  )
}

export default Home
