import React from 'react'
import data from './data'

import {
  Container,
  GalleryContainer,
  Img,
  TextContainer,
  Text,
  Details,
} from './GalleryELements'

function Gallery() {
  return (
    <React.Fragment>
      <Container>
        {data.map((i) => {
          return (
            <React.Fragment>
              <GalleryContainer
                key={i.key}
                columnStart={i.columnStart}
                columnEnd={i.columnEnd}
                rowStart={i.rowStart}
                rowEnd={i.rowEnd}
                minRowStart={i.minRowStart}
                minRowEnd={i.minRowEnd}
                minColumnStart={i.minColumnStart}
                minColumnEnd={i.minColumnEnd}
              >
                <TextContainer img={i.img} alt={i.alt}>
                  <Img src={i.img} alt={i.alt} />
                  <Text>{i.alt}</Text>
                  <Details>{i.details}</Details>
                </TextContainer>
              </GalleryContainer>
            </React.Fragment>
          )
        })}
      </Container>
    </React.Fragment>
  )
}

export default Gallery
