import React from 'react'
import {
  FooterContainer,
  Row1,
  Row2,
  ShareContainer,
  LinksContainer,
  Share,
  Links,
  Heading,
  SponserText,
  ImageContainer,
  Img,
  Wrapper,
} from './FooterElements'
import { SiInstagram } from 'react-icons/si'
import { RiFacebookCircleFill } from 'react-icons/ri'
import { HiOutlineMail } from 'react-icons/hi'

const share = {
  width: '30px',
  height: '30px',
  padding: '0px 20px',
}

const Footer = () => {
  return (
    <React.Fragment>
      <FooterContainer>
        <Row1>
          <Wrapper>
            <Heading>Contant us</Heading>
            <ShareContainer>
              <Share href='https://www.instagram.com/urbanpathways.fijiyouth/'>
                <SiInstagram style={share} />
              </Share>
              <Share href='https://www.facebook.com/fijiyouthculture'>
                <RiFacebookCircleFill style={share} />
              </Share>
              <Share href='https://www.fijiyouthculture@gmail.com'>
                <HiOutlineMail style={share} />
              </Share>
            </ShareContainer>
          </Wrapper>
          <Wrapper>
            <Heading>Links </Heading>
            <LinksContainer>
              <Links to='about'>About</Links>
              <Links to='team'>Team</Links>
              <Links to='partners'>Partners</Links>
              <Links to='blogs'>Blogs</Links>
              <Links to='gallery'>Gallery</Links>
            </LinksContainer>
          </Wrapper>
        </Row1>
        <Row2>
          <SponserText>
            This project is funded by the British Academy’s Youth Futures
            Programme, supported under the UK Government’s Global Challenges
            Research Fund.
          </SponserText>
          <ImageContainer>
            <Img src='uaeLogo.jpg' alt='UAE Logo' />
            <Img src='sruLogo.jpg' alt='' />
            <Img src='pcLogo.jpg' alt='PC logo' />
            <Img src='uspLogo.jpg' alt='' />
            <Img src='museumLogo.png' alt='' />
            <Img src='vouLogo.png' alt='' />
          </ImageContainer>
        </Row2>
      </FooterContainer>
    </React.Fragment>
  )
}

export default Footer
