import styled from 'styled-components'
import { Link as LinkRouter } from 'react-router-dom'

export const FooterContainer = styled.div`
  display: block;
  margin-left: auto;
  margin-right: auto;
  background: #f5f5f7;
  width: 100%;
  height: fit-content;
  background-image: url('background.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  @media screen and (max-width: 768px) {
    margin-top: 15px;
    margin-left: 0px;
    width: 96%;
    padding-right: 20px;
    padding-left: 2px;
  }
  @media screen and (max-width: 400px) {
    margin-top: 250px;
    margin-left: 0px;
    width: 96%;
    padding-right: 20px;
    padding-left: 2px;
  }
`

export const Row1 = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  width: 100%;
  height: 200px;
  justify-content: center;
  align-items: center;
  padding-bottom: 50px;
  padding-top: 20px;
  width: 95%;
  @media screen and (max-width: 450px) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    padding-bottom: 150px;
  }
`
export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  @media screen and (max-width: 450px) {
    margin-bottom: 30px;
  } ;
`
export const ShareContainer = styled.div`
  display: flex;
  justify-content: center;
  padding: 0px 20px;
`
export const Share = styled.a`
  color: black;
  &:hover {
    color: ${({ href }) =>
      href === 'https://www.instagram.com/urbanpathways.fijiyouth/'
        ? '#078DE7'
        : href === 'https://www.facebook.com/fijiyouthculture'
        ? '#01BF71'
        : href === 'https://www.fijiyouthculture@gmail.com'
        ? '#704A3E'
        : '#078DE7'};
  }
`

export const LinksContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-content: flex-end;
`
export const Links = styled(LinkRouter)`
  font-size: 16px;
  width: 80%;
  align-self: center;
  text-align: center;
  padding-bottom: 10px;
  text-decoration: none;
  color: #000000;
  cursor: pointer;
  &:hover {
    color: ${({ to }) =>
      to === 'about'
        ? 'coral'
        : to === 'team'
        ? '#01BF71'
        : to === 'partners'
        ? '#704A3E'
        : to === 'blogs'
        ? '#078DE7'
        : '#F3E603'};
  }
  @media screen and (max-width: 450px) {
    font-size: 15px;
  }
`

export const Row2 = styled.div`
  display: flex;
  flex-direction: column;
  width: 95%;
`
export const SponserText = styled.h1`
  margin-bottom: 24px;
  font-size: 14px;
  line-height: 1.1;
  font-weight: 600;
  text-align: center;

  @media screen and (max-width: 480px) {
    font-size: 12px;
  }
`

export const Heading = styled.h1`
  margin-bottom: 24px;
  font-size: 20px;
  line-height: 1.1;
  font-weight: 600;
  text-align: center;

  @media screen and (max-width: 480px) {
    font-size: 20px;
  }
`

export const ImageContainer = styled.div`
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  align-self: center;
  justify-content: center;
  margin-bottom: 20px;
  gap: 20px;
  @media screen and (max-width: 768px) {
    grid-template-rows: repeat(1fr);
    grid-template-columns: 1fr 1fr;
    width: 20%;
  }
`

export const Img = styled.img`
  width: 80px;
  height: 60px;
  object-fit: fill;
  background: #ffff;
  @media screen and (max-width: 540px) {
    width: 100px;
    height: 100px;
  }
`
