import React from 'react'
import {
  PartnerContainer,
  Header,
  Subtext,
  ImageContainer,
  PartnerName,
  Img,
} from './PartnersElements'

function Partner() {
  return (
    <React.Fragment>
      <PartnerContainer>
        <Header> Our Partners</Header>
        <Subtext>
          {' '}
          Urban Pathways: Fiji. Youth. Arts. Culture. brings together visual and
          intangible arts, anthropology, education, museum studies and marine
          studies, and combines them with Fiji’s urban youth perspectives and
          experiences of what culture is and means to them. The project partners
          are:
        </Subtext>
        <ImageContainer>
          <PartnerName
            href='https://www.uea.ac.uk/'
            target='_blank'
            theme='green'
          >
            University of East Anglia
          </PartnerName>
          <Img src='uaeLogo.jpg' alt='UAE Logo' />
        </ImageContainer>

        <ImageContainer>
          <PartnerName
            href='http://www.sru.uea.ac.uk/'
            target='_blank'
            theme='yellow'
          >
            Sainsbury Research Unit
          </PartnerName>
          <Img src='sruLogo.jpg' alt='SRC logo' />
        </ImageContainer>

        <ImageContainer>
          <PartnerName
            href='https://www.spc.int/'
            target='_blank'
            theme='brown'
          >
            The Pacific Community
          </PartnerName>
          <Img src='pcLogo.jpg' alt='PC logo' />
        </ImageContainer>

        <ImageContainer>
          <PartnerName
            href='https://www.usp.ac.fj/'
            target='_blank'
            theme='blue'
          >
            The University of the South Pacific
          </PartnerName>
          <Img src='uspLogo.jpg' alt='USP' />
        </ImageContainer>

        <ImageContainer>
          <PartnerName
            href='https://fijimuseum.org.fj/'
            target='_blank'
            theme='brown'
          >
            The Fiji Museum
          </PartnerName>
          <Img src='museumLogo.png' alt='' />
        </ImageContainer>

        <ImageContainer>
          <PartnerName
            href='https://voufiji.com/'
            target='_blank'
            theme='yellow'
          >
            VOU
          </PartnerName>
          <Img src='vouLogo.png' alt='' />
        </ImageContainer>
      </PartnerContainer>
    </React.Fragment>
  )
}

export default Partner
