import React from 'react'
import { FaBars } from 'react-icons/fa'
import logoImg from '../../images/logo2.png'
import {
  NavLogo,
  NavbarContainer,
  Nav,
  MobileIcons,
  NavMenu,
  NavItem,
  NavLinks,
  NavBtn,
  NavBtnLink,
  Logo,
} from './NavbarElements'

const Navbar = ({ toggle, isOpen }) => {
  return (
    <React.Fragment>
      <Nav>
        <NavbarContainer onClick={toggle}>
          <NavLogo to='/'>
            <Logo src={logoImg} type='image/png' /> Urban Pathways
          </NavLogo>
          <MobileIcons>
            <FaBars onClick={toggle} />
          </MobileIcons>
          <NavMenu>
            <NavItem>
              <NavLinks to='/about' theme='yellow'>
                {' '}
                About
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to='/team' theme='green'>
                {' '}
                Team
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to='/partners' theme='brown'>
                {' '}
                Partners
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to='/blogs' theme='blue'>
                {' '}
                Blogs
              </NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to='/gallery'> Gallery</NavLinks>
            </NavItem>
          </NavMenu>
          <NavBtn>
            <NavBtnLink to='https://www.facebook.com/'>Join Us </NavBtnLink>
          </NavBtn>
        </NavbarContainer>
      </Nav>
    </React.Fragment>
  )
}

export default Navbar
