import React from 'react'
import {
  HeroContainer,
  HeroBg,
  VideoBg,
  HeroContent,
  HeroH1,
  HeroP,
} from './HeroElements'
import video from '../../videos/beach.mp4'

const HeroSection = () => {
  return (
    <HeroContainer>
      <HeroBg>
        <VideoBg autoPlay loop muted src={video} type='video/mp4' />
      </HeroBg>
      <HeroContent>
        <HeroH1>Urban Pathways</HeroH1>
        <HeroP>Youth.Arts.Culture</HeroP>
      </HeroContent>
    </HeroContainer>
  )
}

export default HeroSection
