import styled from 'styled-components'
import { Link as LinkRouter } from 'react-router-dom'

export const BlogContainer = styled.div`
  padding-top: 80px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background: white;
  width: 95%;
  height: 1200px;
  @media screen and (max-width: 768px) {
    margin-left: 0px;
    width: 95%;
    padding-right: 20px;
    height: 2100px;
  }
  @media screen and (max-width: 400px) {
    margin-left: 0px;
    width: 95%;
    padding-right: 20px;
    height: 2200px;
  }
`
export const Topline = styled.p`
  color: ${({ theme }) =>
    theme === 'yellow'
      ? '#F3E603'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 1.4px;
  @media screen and (max-width: 768px) {
    margin-left: 30px;
  }
`

export const BlogHeading = styled.h1`
  font-size: 48px;
  line-height: 1.1;
  font-weight: 600;
  @media screen and (max-width: 768px) {
    margin-left: 30px;
  }
`

export const BlogText = styled.p`
  font-size: 20px;
  padding-bottom: 20px;
  @media screen and (max-width: 768px) {
    margin-left: 30px;
  }
`

export const BlogWrapper = styled.div`
  padding-top: 30px;
  width: 100%;
  height: 800px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  align-items: center;
  gap: 20px;

  @media screen and (max-width: 768px) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    width: 100%;
  }
`

export const Col = styled.div`
  background: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-bottom: 20px;
  box-shadow: 10px 4px 8px 2px rgba(0, 0, 0, 0.2),
    0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 10px;
  height: 650px;
  @media screen and (max-width: 768px) {
    width: 85%;
    margin-left: 30px;
    margin-bottom: 0px;
  }
`

export const Header = styled.h2`
  font-size: 20px;
  padding-left: 15px;
  padding-right: 15px;
`

export const Date = styled.p`
  font: 24px;
  color: ${({ theme }) =>
    theme === 'yellow'
      ? '#F3E603'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
`

export const Subtext = styled.p`
  font-size: 15px;
  padding-left: 10px;
  padding-right: 10px;
`

export const Img = styled.img`
  width: 100%;
  height: 320px;
  border-radius: 10px 10px 0px 0px;
  object-fit: cover;
  border: 1px solid rgba(0, 0, 0, 0.19);

  @media screen and (max-width: 768px) {
    object-fit: cover;
    object-position: 40% 40%;
  }
`
export const Button = styled(LinkRouter)`
  width: 100px;
  border-radius: 50px;
  background: ${({ theme }) =>
    theme === 'yellow'
      ? '#F3E603'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
  white-space: nowrap;
  padding: 16px 64px;
  color: #010606;
  font-size: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  text-align: center;

  &:hover {
    transition: all 0.2s ease-in-out;

    box-shadow: 10px 4px 8px 2px rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
`
export const BlogButton = styled(LinkRouter)`
  width: 100px;
  border-radius: 50px;
  background: ${({ theme }) =>
    theme === 'yellow'
      ? '#F3E603'
      : theme === 'green'
      ? '#01BF71'
      : theme === 'brown'
      ? '#704A3E'
      : '#078DE7'};
  white-space: nowrap;
  padding: 16px 64px;
  color: #010606;
  font-size: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  text-align: center;
  align-self: flex-start;
  &:hover {
    transition: all 0.2s ease-in-out;
    background: #fff;
    color: #010606;
    border: 1px solid #078de7;
  }
  @media screen and (max-width: 768px) {
    margin-left: 30px;
  }
`
