import styled from 'styled-components'

export const TeamContainer = styled.div`
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr;
  width: 100vw;
  height: 2500px;
`

export const TeamWrapper = styled.div`
  width: 100%;
  display: grid;
  background: ${({ background }) =>
    background === 'vou'
      ? '#ffff'
      : background === 'usp'
      ? 'green'
      : background === 'museum'
      ? 'brown'
      : '#ffff'};
  display: flex;
  flex-direction: column;
  background: green;
`

export const HeadWrapper = styled.div`
  width: 90%;
  background: purple;
`

export const Header = styled.h2`
  font-size: 20px;
`

export const Logo = styled.img`
  width: 100px;
  height: 100px;
`

export const BodyWrapper = styled.div`
  height: fit-content;
  background: green;
`
export const Leader = styled.div`
  align-self: center;
  background: blue;
`

export const MemberContainer = styled.div`
  align-self: center;
  background: pink;
`

export const Member = styled.div`
  width: 300px;
  height: 300px;
  background: yellow;
`

export const MemberImage = styled.img`
  height: 200px;
  width: 200px;
`

export const MemberText = styled.h2`
  font-size: 16px;
`

export const Designation = styled.p`
  font-size: 12px;
`

export const LeaderImage = styled.img`
  height: 200px;
  width: 200px;
`

export const LeaderText = styled.h2`
  font-size: 16px;
`

export const LeaderDesignation = styled.p`
  font-size: 12px;
`
