import styled from 'styled-components'

export const Container = styled.div`
  width: 80vw;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 300px 300px 300px 300px;
  gap: 10px;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  height: 1300px;

  @media screen and (max-width: 768px) {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 200px 200px 200px 200px;
  }
`

export const GalleryContainer = styled.div`
  grid-column-start: ${({ columnStart }) =>
    columnStart > 0 ? columnStart : columnStart};
  grid-column-end: ${({ columnEnd }) =>
    columnEnd > 0 ? columnEnd : columnEnd};
  grid-row-start: ${({ rowStart }) => (rowStart > 0 ? rowStart : rowStart)};
  grid-row-end: ${({ rowEnd }) => (rowEnd > 0 ? rowEnd : rowEnd)};

  position: relative;

  @media screen and (max-width: 768px) {
    grid-column-start: ${({ minColumnStart }) =>
      minColumnStart > 0 ? minColumnStart : minColumnStart};
    grid-column-end: ${({ minColumnEnd }) =>
      minColumnEnd > 0 ? minColumnEnd : minColumnEnd};
    grid-row-start: ${({ minRowStart }) =>
      minRowStart > 0 ? minRowStart : minRowStart};
    grid-row-end: ${({ minRowEnd }) => (minRowEnd > 0 ? minRowEnd : minRowEnd)};
  }
`
export const Img = styled.img`
  width: 100%;
  height: 100%;

  position: absolute;
  object-fit: cover;

  &:hover {
    /* filter: opacity(20%); */
  }
`
export const TextContainer = styled.div`
  width: 100%;
  height: 100%;
  display: block;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: transparent;

  &:hover {
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
      1px 1px 0 #000;
    color: white;
  }
`

export const Text = styled.h2`
  font-size: 5vw;
  position: absolute;
`

export const Details = styled.p`
  font-size: 10px;
  position: absolute;
`
