import React from 'react'

import {
  InfoContainer,
  InfoWrapper,
  InfoRow,
  Column1,
  Column2,
  Topline,
  Heading,
  ImgWrap,
  Img,
  BtnWrapper,
  Subtext,
  Button,
} from './infoElements'

const InfoSection = ({
  headLine,
  darktext,
  description,
  buttonLabel,
  img,
  alt,
  lightBg,
  id,
  imgStart,
  topLine,
  lightText,
  theme,
}) => {
  return (
    <React.Fragment>
      <InfoContainer lightBg={lightBg} id={id}>
        <InfoWrapper>
          <InfoRow imgStart={imgStart}>
            <Column1>
              <Topline theme={theme}>{topLine}</Topline>
              <Heading lightText={lightText}>{headLine}</Heading>
              <Subtext darktext={darktext}>{description}</Subtext>
              <BtnWrapper>
                <Button to='home' theme={theme}>
                  {buttonLabel}{' '}
                </Button>
              </BtnWrapper>
            </Column1>
            <Column2>
              <ImgWrap>
                <Img src={img} alt={alt} />
              </ImgWrap>
            </Column2>
          </InfoRow>
        </InfoWrapper>
      </InfoContainer>
    </React.Fragment>
  )
}

export default InfoSection
